.nav {
    position: relative;
    height: 10dvh;
    max-height: 100px;
    width: 99.5dvw;
    margin: 0;
    background-color: #76abae;
    border-radius: 0 0 15px 15px;
}

.link {
    display: flex;
    height: 100%;
    align-items: center;
}

.link a {
    margin-left: 30px;
    font-size: 2em;
    font-weight: 700;
    text-decoration: none;
    color: #222831;
}