.container {
    position: relative;
    width: 500px;
    height: 345px;
    margin: 30px;
    padding: 10px;
    transition: all .3s ease-in-out;
    float: left;
    box-shadow: 5px 5px 15px black;
    background-color: #31363f;
    color: #eeeeee;
    border-radius: 5px 5px;
}

.container img {
    float: left;
    margin-right: 20px;
    box-shadow: 1px 1px 5px #222831;
}

.content {
    position: relative;
    float: left;
    width: 250px;
    height: 345px;
}

.ratingBack {
    transition: all .3s ease-in-out;
    position: absolute;
    text-align: center;
    top: 10px;
    left: 10px;
    width: 230px;
    height: 345px;
    background-color: rgba(255, 255, 255, .4);
    opacity: 0;
    backdrop-filter: blur(2px);
}

.rating {
    transition: all .3s ease-in-out;
    position: absolute;
    top: 250px;
    width: 95%;
    text-align: center;
    font-size: 2.3em;
    font-weight: 700;
}

.container:hover {
    transform: translateX(-3px) translateY(-3px);
    box-shadow: 8px 8px 15px black;
}

.ratingBack:hover {
    opacity: 1;
}

.ratingBack:hover>.rating {
    font-size: 2em;
}