.onLoad {
    transition: all .5s;
    width: 100%;
    height: 100%;
    text-align: center;
    align-content: center;
    margin-top: 300px;
    color: #76abae;
}

.progress {
    margin: auto;
    padding: 3px;
    text-align: center;
    height: 22px;
    width: 300px;
    border-radius: 9px;
    border: 1px solid #76abae;
}

.progressBar {
    transition: all .5s;
    float: left;
    background-color: #76abae;
    width: 100%;
    height: 100%;
    border-radius: 7px;
}