.openContainer {
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(34, 40, 49, .6);
    z-index: 99;
    display: grid;
    place-items: center;
    animation: modal-bg-show .3s;
}

.closeContainer {
    display: none;
}

.modal {
    position: relative;
    width: 75dvw;
    max-width: 1200px;
    height: 75dvh;
    animation: modal-show .3s;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 1px 1px 3px #222831;
}

.gradientTop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 40%;
    background: linear-gradient(0deg, rgba(34, 40, 49, 0) 0%, rgba(34, 40, 49, 0.6) 65%, rgba(34, 40, 49, 1) 100%);
    color: #eeeeee;
}

.gradientBot {
    position: absolute;
    top: 40%;
    left: 0;
    width: 100%;
    height: 62%;
    background: linear-gradient(0deg, rgba(34, 40, 49, 1) 0%, rgba(34, 40, 49, 0.6) 80%, rgba(34, 40, 49, 0) 100%);
    color: #eeeeee;
}

.gradientTop h1 {
    position: relative;
    float: left;
    margin: 50px 0 0 50px
}

.gradientTop .rating {
    margin-left: 15px;
}

.gradientTop button {
    position: relative;
    float: right;
    outline: none;
    cursor: pointer;
    border: 0;
    margin: 15px 15px 0 0;
    font-weight: 700;
    background: rgba(34, 40, 49, 0);
    font-size: 2.5em;
    color: #eeeeee;
}

.gradientBot img {
    position: relative;
    margin-left: 50px;
    float: left;
    box-shadow: 1px 1px 3px black;
}

.gradientBot h2 {
    float: left;
    width: 800px;
    margin-right: auto;
    margin-left: 30px;

}

.gradientBot p {
    float: left;
    width: 800px;
    margin-left: 30px;
    font-size: 1.2em;
}

.gradientBot .link {
    transition: all .3s;
    float: left;
    margin-left: 30px;
    font-size: 1.1em;
    text-decoration: underline;
    color: #eeeeee;
}

.gradientBot .link:hover {
    color: #76abae;
}

@keyframes modal-bg-show {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes modal-show {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}